<template>
  <div>
      <h2>我是登陆页面</h2>
      <p>我是登陆内容,嘿嘿嘿</p>
      <p>用户名称:{{userID}}</p>
      <p>将url后面user参数拿到(和上面用户名称方法一样):{{$route.params.userid}}</p>
  </div>
</template>

<script>
export default {
    name:"User",
    computed: {
        userID(){
            // 这里的$route(不带r)  当前是谁处于活跃状态拿到的就是谁
            return this.$route.params.userid
        }
    }
}
</script>

